h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
    line-height: 1.2;
}

h1, h2 {
    font-size: 40px;
}

ul {
    list-style: none;
}

img {
    max-width: 100%;
    vertical-align: bottom;
}

* {
    box-sizing: border-box;
    outline: none;
}

figure, p {
    margin: 0;
}

p {
    line-height: 1.8;
    margin: 0;
}

body {
    font-family: 'Cabin', sans-serif;
    line-height: 1.5;
    font-size: 16px;
    font-weight: 400;
}

input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}



table.table {
    border-collapse: collapse;
    width: 100%;
    td, th {
        border: 1px solid #ddd;
        padding: 10px 20px;
    }
}



.overflow-hidden {
    overflow: hidden;
    .main-wrapper {
        -webkit-filter: blur(2px);
        filter: blur(2px);
    }
}

a {
    text-decoration: none;
    &.link {
        color: #3591D0;
        border-bottom: 1px solid transparent;
        transition: .2s border-color;
        &:hover {
            border-color: inherit;
        }
    }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type=number] {
    -moz-appearance: textfield;
}

.container {
    @include container();
    @include sm(max-width, rem(510));
    @include md(max-width, rem(736));
    @include lg(max-width, rem(1024));
    @include xl(max-width, rem(1290));
}

.button {
    color: #fff;
    display: inline-block;
    border-radius: 5px;
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    text-align: center;
    font-size: 17px;
    padding: 22.5px 20px;
    width: 100%;
    min-width: 220px;

    @media (min-width: $break_md) {
        font-size: 22px;
        padding: 18.5px 20px;
        width: auto;
    }

    &.button-red {
        background: #EA405E;
        background: linear-gradient(74.47deg, #DD5645 0%, #EA405E 100%);
        &:hover {
            background: linear-gradient(81.6deg, #FE3659 0%, #FF7160 100%);
        }
        &:active {
            background: #FF5F5E;
        }
    }
    &.button-blue {
        background: #3591D0;
        background: linear-gradient(77.47deg, #3591D0 0%, #26B8DC 100%);
        &:hover {
            background: linear-gradient(81.6deg, #26B8DC 0%, #3591D0 100%);
        }
        &:active {
            background: #3591D0;
        }

    }
    &.button-emoji {
        &:after {
            content: "";
            width: 24px;
            height: 24px;
            display: inline-block;
            margin-left: 10px;
            vertical-align: text-top;
            background: url(../img/svg/emoji.svg) center/100% no-repeat;
        }
    }
    &.button-mini {
        font-size: 18px;
        padding-top: 14px;
        padding-bottom: 14px;
        border-radius: 3px;
    }
    &.button-loading {
        @include gradientAnimation();
    }
}

.icon-list {
    li {
        display: flex;
        align-items: center;
        &:before {
            content: "";
            width: 12px;
            min-width: 12px;
            height: 12px;
            display: inline-block;
            background: url(../img/svg/icon-list.svg) center no-repeat;
            margin-right: 10px;
        }
    }
}

.icon-font, .stars-rating {
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.stars-rating {
    display: inline-block;
    position: relative;
    letter-spacing: 0.8px;
    font-size: 18px;

    &:after {
        content: "\e900\e900\e900\e900\e900";
        color: $orange;
        opacity: .3;
        z-index: 0;
    }
    span {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        overflow: hidden;
        z-index: 1;
        &:after {
            content: "\e900\e900\e900\e900\e900";
            position: absolute;
            left: 0;
            top: 0;
            color: $orange;
        }
    }
}

.section-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
    position: relative;
    z-index: 1;

    .section-header__title {
        h2 {
            font-size: 30px;
        }
    }
    .section-header__subtitle {
        font-size: 18px;
        max-width: 720px;

    }
    div:not(:last-child) {
        margin-bottom: 30px;
    }
    &.section-header__light {
        color: #fff;
    }
}



.modal {
    z-index: -1;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -webkit-overflow-scrolling: touch;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: visibility 0s 0.3s, opacity 0.3s 0s, z-index 0s 0.3s;

    &.modal-is-visible {
        z-index: 10;
        opacity: 1;
        visibility: visible;
        pointer-events: inherit;
    }

    .modal__body {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #fff;
        z-index: 11;
        width: 90%;
        border-radius: 5px;



        @media (min-width: $break_md) {
            width: auto;
            height: auto!important;
            max-height: 85vh;
            max-width: 920px;
        }
    }

    .modal__title {
        margin-bottom: 30px;
        font-size: 22px;
        font-weight: 500;
        @media (min-width: $break_md) {
            text-align: center;
            font-size: 30px;
        }
    }


    .modal__C {
        padding: 20px;
        line-height: 1.5;
        max-height: inherit;
        overflow-y: auto;
        display: none;
        @media (min-width: $break_md) {
            min-width: 720px;
        }
        @media (min-width: $break_lg) {
            min-width: 830px;
        }


        &::-webkit-scrollbar {
            width: 6px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 4px;
            background: #ccc;
        }
        &::-webkit-scrollbar-track {
            border-radius: 3px;
        }

        &.modal-is-visible {
            display: block;
        }

        p, ul, li {
            margin-bottom: 10px;
        }
        ul {
            list-style: disc;
            padding-left: 18px;
        }
    }

    .modal__video {
        display: none;
        &.modal-is-visible {
            display: block;
        }
    }
    .modal__form {
        display: none;
        &.modal-is-visible {
            align-items: center;
            justify-content: center;
            height: 100%;
            display: flex;
        }
    }

    .modal__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.5);
    }
    .modal__close {
        position: absolute;
        height: 32px;
        width: 32px;
        z-index: 11;
        background-color: #fff;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 50%;
        cursor: pointer;
        top: -14px;
        right: -14px;



        &:after, &:before {
            content: "";
            position: absolute;
            width: 45%;
            height: 2px;
            background-color: #444;
            top: 50%;
            left: 50%;
            transform: rotate(45deg) translate(-5px, 4px);
            border-radius: 3px;
        }
        &:before {
            transform: rotate(-45deg) translate(-4px, -5px);
        }
    }
}

.modal-form {
    width: 100%;
    max-width: 448px;
    padding: 20px;
    margin: 0 auto;

    .modal-form__header {
        margin-bottom: 10px;
        font-size: 18px;
        font-weight: 500;
        line-height: 1.3;
        text-align: center;

    }
    .modal-form__body {
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;

        .input {
            font-size: 16px;
            border: 1px solid #bdbdbd;
            border-radius: 3px;
            margin-bottom: 10px;
            padding: 15px;
            box-shadow: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            &.js-input-error {
                border-color: #ff431e;
            }
            &.js-input-success {
                border-color: #33b563;
            }
        }
    }
    .modal-form__footer {
        font-size: 14px;
        text-align: center;
    }
}

@keyframes placeHolderShimmer{
    0%{
        background-position: -500px 0
    }
    100%{
        background-position: 500px 0
    }
}


.slider-button-prev, .slider-button-next {
    width: 25px;
    height: 30px;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    background-color: #fff;
    z-index: 1;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    outline: none;
    @media  (min-width: $break_lg) {
        width: 50px;
        height: 60px;
        top: 50%;
    }

    &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 10px;
        height: 15px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 80%;
        @media  (min-width: $break_lg) {
            width: 20px;
            height: 30px;

        }
    }
    &.swiper-button-disabled {
        opacity: .0;
        box-shadow: none;
        pointer-events: none;
    }
    &:active, &:focus {
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15);
    }
}

.slider-button-prev {
    left: 10px;
    &:after {
        transform: translate(-60%, -50%);
        background-image: url(../img/svg/icon-left.svg);
    }
}

.slider-button-next {
    right: 10px;
    &:after {
        transform: translate(-40%, -50%);
        background-image: url(../img/svg/icon-right.svg);
    }
}

