.footer {
		background-color: #21252a;
		color: #fff;

		.footer-top {
				background: rgba(255, 255, 255, 0.2);
				.footer-top__wrap {
						display: flex;
						align-items: center;
						padding: 32px 0;
						flex-direction: column;
						@media (min-width: $break_md) {
								flex-direction: row;
								justify-content: center;
						}
						span {
								margin-bottom: 10px;
								@media (min-width: $break_md) {
										margin-bottom: 0;
										margin-left: 15px;
										margin-right: 15px;
								}
								a {
										color: #fff;
								}
						}
				}

		}
		.footer__menu {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				padding: 50px 0;
				li {
						margin: 0 40px 40px;
						text-align: center;
						flex: 1;
						@media (min-width: $break_md) {
								margin-bottom: 0;
								text-align: left;
								flex: unset;
						}

						a {
								color: #fff;
						}
				}
		}
		.footer-bottom {
				border-top: 1px solid #333;
				padding-bottom: 20px;
				padding-top: 20px;
				text-align: center;
		}
}