.section-grid {
		padding-top: 50px;
		padding-bottom: 50px;
		background:#000 linear-gradient(84.89deg, #19455f 0%, #5f203e 100%);
		@media (min-width: $break_xl ) {
				padding-top: 100px;
				padding-bottom: 100px;
		}

		.grid-wrap {
				color: #fff;
				margin: 0 auto;

				@media (min-width: $break_xl ) {
						@include size(10);
				}

				.grid-row {
						@include row-flex();
						flex-direction: column;
						@media (min-width: $break_md ) {
								flex-direction: row;
								align-items: center;
						}

						&:not(:last-child) {
								margin-bottom: 30px;
								@media (min-width: $break_md ) {
										margin-bottom: 100px;
								}
						}

						.grid-row__L{
								@include col();
								@include size(12);
								margin-bottom: 30px;
								@media (min-width: $break_md ) {
										margin-bottom: 0;
										@include size(6);
								}
								@media (min-width: $break_xl ) {
										@include size(5);
								}


						}
						.grid-row__R{
								@include col();
								@include size(12);
								@media (min-width: $break_md ) {
										@include size(6);
								}

								.grid-row__title {
										font-size: 22px;
										margin-bottom: 20px;
								}
								.grid-row__text {
										margin-bottom: 20px;
								}
								.grid-row__list {
										li {
												&:not(:last-child) {
														margin-bottom: 20px;
												}
										}
								}
						}

						&:nth-child(even) {
								.grid-row__L {
										@media (min-width: $break_md ) {
												order: 2;
										}
								}
						}
				}
		}
}