.product-section {
		padding-top: 50px;
		padding-bottom: 50px;
		background-color: #f8f8f8;
		@media (min-width: $break_xl ) {
				padding-top: 100px;
				padding-bottom: 100px;
		}

		.product-wrap {
				@include row-flex();
				flex-direction: column;
				@media (min-width: $break_md) {
						flex-direction: row;
				}
				@media (min-width: $break_xl) {
						align-items: center;
				}

				.product-wrap__block {
						@include col();
						@include size(12);

						@media (min-width: $break_md) {
								@include size(6);
						}

				}

				.product-wrap__left {
						margin-bottom: 30px;
						@media (min-width: $break_md) {
								margin-bottom: 0;
						}
				}


				.product-gallery {
						width: 100%;
						position: relative;


						.product-gallery__wrap {
								.product-gallery__slide {
										border-radius: 10px;
										overflow: hidden;
										display: flex;
										justify-content: center;
										align-items: center;
										img {
												width: 100%;
										}
								}
						}


						.product-gallery__pagination {
								display: flex;
								justify-content: center;
						}

						.product-gallery__pagination {
								padding-top: 30px;
								.swiper-pagination-bullet {
										width: 10px;
										height: 10px;
										margin: 0 7.5px;
										&.swiper-pagination-bullet-active {
												background: linear-gradient(87.07deg, #DD5645 0%, #EA405E 100%);
										}
										@media  (min-width: $break_lg) {
												width: 14px;
												height: 14px;
												margin: 0 10px;
										}

								}
						}

						.product-gallery__onsale {
								position: absolute;
								right: 0;
								z-index: 1;
								color: #fff;
								background: linear-gradient(84.25deg, #DD5645 0%, #EA405E 100%);
								border-radius: 10px 0px 0px 10px;
								font-size: 16px;
								top: 46px;
								padding: 3px 12px;
								@media (min-width: $break_lg) {
										top: 86px;
										font-size: 30px;
										padding: 8px 20px;
								}
						}

				}


				.product__content {
						display: flex;
						flex-direction: column;

						.product__title {
								margin-bottom: 10px;
								h1 {
										font-size: 30px;
										@media (min-width: $break_xl) {
												font-size: 40px;
										}
								}
						}

						.product__rating {
								margin-bottom: 30px;
						}

						.product__description {
								margin-bottom: 30px;
								ul.icon-list {
										padding-top: 30px;
										li {
												margin-bottom: 16px;
												font-weight: 500;
										}
								}
						}

						.product__options {
								margin-bottom: 30px;
						}

						.product-variations__header {
								margin-bottom: 20px;
						}

						.product-variations__listing {
								display: flex;
								flex-wrap: wrap;

								.product-variations__item {
										border-radius: 50%;
										width: 60px;
										height: 60px;
										margin-bottom: 8px;
										position: relative;
										cursor: pointer;
										border: 2px solid transparent;
										transition: .1s border-color;
										&:not(:last-child) {
												margin-right: 8px;
										}

										&:before {
												content: "";
												position: absolute;
												background-color: #fff;
												top: 2px;
												left: 2px;
												right: 2px;
												bottom: 2px;
												z-index: 0;
												border-radius: 50%;
										}

										&:hover {
												border-color: #e8e8e8;
										}

										.product-variations__image {
												position: relative;
												border-radius: 50%;
												z-index: 1;
										}

								}


								.product-variations__active {
										border-color: #DD5645;
										border-image-slice: 10;
										border-image-repeat: round;
										border-image-width: 10px;
										border-image-source: url(../img/svg/gradient-radius.svg);
										@include icon-font;

										&:after {
												content: "\e901";
												position: absolute;
												top: 0;
												right: 0;
												transform: translate(5px, 0px);
												font-size: 8px;
												text-align: center;
												color: #fff;
												width: 20px;
												height: 14px;
												padding-top: 6px;
												border-radius: 50%;
												border: 2px solid #fff;
												background: #EA405E linear-gradient(87.07deg, #DD5645 0%, #EA405E 100%);
												z-index: 2;
										}
								}

						}

						.product__price {
								display: flex;
								align-items: center;
								margin-bottom: 30px;

								.product-price__new {
										font-size: 30px;
										margin-right: 20px;
										font-weight: 600;

										color: #DD5645;
										background: linear-gradient(78.59deg, #DD5645 0%, #EA405E 100%);
										-webkit-background-clip: text;
										-webkit-text-fill-color: transparent;

								}
								.product-price__old {
									color: #828282;
										font-size: 18px;
								}
						}

						.product__controls {
								display: flex;


								.product-controls__button {
										flex: 1;

										.button {
												font-size: 17px;
												padding: 22.5px 10px;
												width: 100%;
												@media (min-width: $break_lg) {
														font-size: 22px;
														padding: 18.5px 40px;
														width: auto;
												}
										}
								}

								.product-controls__quantity {
										margin-right: 10px;
										@media  (min-width: $break_lg) {
												margin-right: 30px;
										}
								}

								.quantity {
										position: relative;
										width: 57px;
										height: 70px;
										border: 1px solid #000;
										border-radius: 5px;
										overflow: hidden;
										@media  (min-width: $break_sm) {
												width: 78px;
										}


										.quantity__button {
												position: absolute;
												right: 0;
												width: 38%;
												height: 50%;
												border: none;
												cursor: pointer;
												font-size: 8px;
												background-color: #fff;
												padding: 0;
												text-align: left;
												@include icon-font;

												&.js-quantity_up {
														top: 0;
														&:after {
																content: "\e903";
														}
												}

												&.js-quantity_down {
														bottom: 0;
														font-size: 9px;
														&:after {
																content: "\e902";
														}
												}
										}

										.quantity__input {
												width: 62%;
												height: 100%;
												padding: 10px 6px;
												margin: 0;
												text-align: center;
												border: none;
												font-weight: 600;
										}
								}

						}


				}



		}
}