.section-faq {
		padding-top: 50px;
		padding-bottom: 50px;
		background-color: #21252a;
		position: relative;
		overflow: hidden;
		@media (min-width: $break_xl ) {
				padding-top: 100px;
				padding-bottom: 100px;
		}

		&:after {
				content: "";
				background: red;
				position: absolute;
				width: 110%;
				height: 162%;
				left: 50%;
				top: 0;
				transform: translateX(-50%);
				z-index: 0;

				background: rgb(233,73,73);
				background: -moz-radial-gradient(circle, rgba(233,73,73,0.3) 0%, rgba(233,74,74,0.1) 50%, rgba(0,0,0,0) 100%);
				background: -webkit-radial-gradient(circle, rgba(233,73,73,0.3) 0%, rgba(233,74,74,0.1) 50%, rgba(0,0,0,0) 100%);
				background: radial-gradient(circle, rgba(233,73,73,0.3) 0%, rgba(233,74,74,0.1) 50%, rgba(0,0,0,0) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#e94949",endColorstr="#000000",GradientType=1);
		}
}

.faq-wrap {

		position: relative;
		z-index: 1;
		color: #fff;

		@media (min-width: $break_md ) {
				@include row-flex();
		}
		@media (min-width: $break_lg ) {
				@include size(10);
				margin: 0 auto;
		}
		@media (min-width: $break_xl ) {
				@include size(8)
		}

		.faq-wrap__column {

				@media (min-width: $break_md ) {
						@include size(6);
						@include col();
				}
		}

		.faq-item {
				display: inline-block;
				width: 100%;
				border-radius: 5px;
				background: rgba(255, 255, 255, 0.2);
				margin-bottom: 10px;
				transition: .1s background-color, .1s border-color;
				border: 1px solid rgba(255, 255, 255, 0.2);

				&.faq-show, &:hover {
						background-color: #fff;
						border-color: rgba(255, 255, 255, 0.6);
						color: #444;
						.faq-item__header {
								&:before, &:after {
										background-color: #444;
								}
						}

				}

				&.faq-show {
						.faq-item__header {
								&:before {
										opacity: 0;
										transform: translateY(-50%) rotate(90deg);
								}
								&:after {
										transform: translateY(-50%) rotate(180deg);
								}
						}

						.faq-item__body {
								display: inline-block;
						}
				}
		}


		.faq-item__header {
				font-weight: 500;
				padding: 18px 40px 18px 20px;
				cursor: pointer;
				position: relative;
				&:before {
						content: "";
						position: absolute;
						right: 22px;
						width: 2px;
						height: 16px;
						background: #fff;
						top: 50%;
						transform: translateY(-50%);
						transition: .2s transform, .1s opacity;
				}
				&:after {
						content: "";
						position: absolute;
						right: 15px;
						width: 16px;
						height: 2px;
						background: #fff;
						top: 50%;
						transform: translateY(-50%);
						transition: .2s transform, .2s opacity;
				}
		}

		.faq-item__body {
				display: none;
				padding: 2px 15px 20px;
		}

}


