.header {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 1;

		.header-wrap {
				height: 90px;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;

				@media (min-width: $break_md ) {
						height: 180px;
				}
				@media (min-width: $break_lg) {
						justify-content: space-between;
						flex-direction: row;
						height: 90px;
				}

				.header-wrap__logo {
						width: 208px;
						height: 50px;
						background: url(../img/svg/logo.svg) center/100% no-repeat;
						@media (min-width: $break_md ) {
								margin-bottom: 40px;
						}
						@media (min-width: $break_lg) {
								margin-bottom: 0;
						}
				}

				.nav {
						display: none;
						align-items: center;

						.nav__li {
								&:not(:last-child) {
										margin-right: 55px;
								}
						}

						.nav__link {
								text-transform: uppercase;
								font-size: 18px;
								font-weight: 500;
								color: #fff;
								border-bottom: 2px solid transparent;
								transition: .2s all;
								&:hover {
										border-color: #fff;
								}
						}

						.nav__button {
								position: relative;
								border: 2px solid #DD5645;
								border-radius: 5px;
								color: #DD5645;
								padding: 11px 17px;

								background: linear-gradient(70.17deg, #DD5645 0%, #EA405E 100%);
								-webkit-background-clip: text;
								-webkit-text-fill-color: transparent;
								border-image-slice: 10;
								border-image-repeat: round;
								border-image-width: 10px;
								border-image-source: url(../img/svg/border_red-gradient.svg);
								&:hover {
										background: linear-gradient(78.25deg, #DD5645 0%, #EA405E 100%);
										color: #fff;
										-webkit-background-clip: inherit;
										-webkit-text-fill-color: inherit;
								}

						}

						@media (min-width: $break_md) {
								display: flex;
						}
				}

		}

		& + section {
				padding-top: 90px;
				@media (min-width: $break_md ) {
						padding-top: 180px;
				}
				@media (min-width: $break_lg ) {
						padding-top: 90px;
				}
		}

}