.features-section {
		background-color: #21252a;
		overflow-x: hidden;
		padding-top: 50px;
		padding-bottom: 50px;

		.features-wrap {

				@include row-flex();

				.feature {
						@include col();


						display: flex;
						align-items: center;
						justify-content: center;
						width: 40%;

						@media (min-width: $break_lg ) and (max-width: $break_xl) {
								flex-direction: column;
								text-align: center;
								justify-content: center;
						}
						@media  (min-width: $break_lg) {
								@include size(3);
								justify-content: flex-start;
						}


						.feature__icon {
								min-width: 60px;
								max-width: 60px;
								margin-right: 15px;
								@media (min-width: $break_lg ) and (max-width: $break_xl) {
										margin-right: 0;
										margin-bottom: 15px;
								}
						}
						.feature__title {
								color: #fff;
								@media  (min-width: $break_xl) {
										max-width: 200px;
								}
						}

						&.swiper-slide {
								margin: 0;
						}

				}
				&.swiper-wrapper {
						flex-wrap: nowrap;
						margin: 0;
				}
		}
		.feature-slider__pagination {
				justify-content: center;
				align-items: center;
				display: flex;
				padding-top: 40px;
				.swiper-pagination-bullet {
						width: 10px;
						height: 10px;
						margin: 0 7.5px;
						background: rgba(255, 255, 255, 0.3);
						&.swiper-pagination-bullet-active {
								background: linear-gradient(87.07deg, #DD5645 0%, #EA405E 100%);
						}

				}
				@media (min-width: $break_lg ) {
						display: none;
				}
		}


}