.intro-section {
		min-height: 650px;
		background-color: #21252a;
		position: relative;
		overflow: hidden;

		.intro-wrap {
				position: relative;
				z-index: 1;
				color: #fff;
				padding-top: 30px;
				padding-bottom: 30px;
				flex-direction: column;

				@include row-flex();

				@media (min-width: $break_md) {
						flex-direction: row;

				}

				.intro__left {
						@include size(12);
						@include col();
						order: 2;

						@media (min-width: $break_md) {
								@include size(6);
								order: 1;
						}
						@media (min-width: $break_lg ){
								@include size(5);
						}
				}

				.intro-inner {
						@include size(12);
						@include col();
						display: flex;
						flex-direction: column;
						margin-bottom: 30px;

						@media (min-width: $break_md) {
								@include size(6);
								margin-bottom: 0;
								order: 2;
						}
						@media (min-width: $break_lg ){
								flex-direction: row;
								@include size(7);
						}

				}

				.intro__center {
						margin-bottom: 30px;

						@media (min-width: $break_lg) {
								display: flex;
								justify-content: center;
								margin-right: 20px;
								margin-bottom: 0;
						}


				}

				.intro__right {
						width: auto;
						@media (min-width: $break_lg) {
								width: 292px;
						}

				}

				.intro-block {

						.intro-block__title {
								margin-bottom: 30px;
								font-size: 26px;
								@media (min-width: $break_md) {
										font-size: 40px;
								}
						}

						.intro-block__price {
								margin-bottom: 30px;
								display: flex;
								align-items: center;
								line-height: 1.3;
								flex-wrap: wrap;



								.intro-price__old {
										font-size: 16px;
										margin-right: 10px;
										@media (min-width: $break_md ) and (max-width: $break_lg) {
												margin-right: 0;
												margin-bottom: 20px;
										}
										@media (min-width: $break_md) {
												margin-right: 20px;
												font-size: 22px;
										}
								}

								.intro-price__new {
										font-weight: 500;
										color: #F2C94C;
										font-size: 22px;

										@media (min-width: $break_md ) and (max-width: $break_xl) {
												font-size: 30px;
										}
										@media (min-width: $break_xl ) {
												font-size: 40px;
										}

								}
						}

						.intro-block__list {
								margin-bottom: 30px;

								li {
										font-weight: 500;
										&:not(:last-child) {
												margin-bottom: 16px;
										}
								}
						}

				}

				.intro-video {
						cursor: pointer;
						position: relative;
						text-align: center;

						&:after {
								content: "";
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%, -50%);
								width: 64px;
								height: 64px;
								border: 3px solid #fff;
								border-radius: 50%;
								background: url(../img/svg/icon-play.svg) 57% 50%/28px no-repeat;
								transition: .1s all;
						}
						&:hover {
								&:after {
										background: url(../img/svg/icon-play-hover.svg) center/110% no-repeat;
								}
						}
						&:active {
								&:after {
										transform: translate(-50%, -50%);
								}
						}
				}

				.intro-review {

						.intro-review__message {
								background-color: #fff;
								color: #444;
								padding: 20px;
								border-radius: 5px;
								position: relative;
								margin-bottom: 25px;
								@media (min-width: $break_md ) and (max-width: $break_xl) {
										padding: 10px;
								}
								@media (min-width: $break_md) {
										margin-bottom: 25px;
										margin-bottom: 30px;
								}



								&:after {
										content: "";
										border: 8px solid transparent;
										border-left: 8px solid #fff;
										border-top: 8px solid #fff;
										position: absolute;
										top: 100%;
										right: 64px;
								}
						}

						.intro-review__author {
								display: flex;
								align-items: center;
								margin-bottom: 25px;


								img {
										width: 60px;
										height: 60px;
										border-radius: 50%;
										margin-right: 10px;
								}
						}

						.intro-rating {
								display: flex;
								align-items: center;
								justify-content: center;
								padding: 12px 0 10px;
								background: linear-gradient(90deg, #DE5447 0%, rgba(222, 84, 71, 0) 100%);
								transform: translateX(-15px);
								width: calc(100% + 30px);

								@media (min-width: $break_sm) {
										transform: none;
										width: auto;
								}

								.intro-rating__medium {
										display: flex;
										flex-direction: column;
										text-align: center;
										margin-right: 5px;
										min-width: 80px;

										strong {
												font-size: 30px;
												line-height: 1;
										}

								}
								.intro-rating__customers {
										display: flex;
										flex-direction: column;

										.intro-stars__stars {
												margin-bottom: 5px;
										}
								}
						}

				}


		}

		&:after {
				content: "";
				background: red;
				position: absolute;
				width: 110%;
				height: 162%;
				left: 50%;
				top: 0;
				transform: translateX(-50%);
				z-index: 0;

				background: rgb(233,73,73);
				background: -moz-radial-gradient(circle, rgba(233,73,73,0.3) 0%, rgba(233,74,74,0.1) 50%, rgba(0,0,0,0) 100%);
				background: -webkit-radial-gradient(circle, rgba(233,73,73,0.3) 0%, rgba(233,74,74,0.1) 50%, rgba(0,0,0,0) 100%);
				background: radial-gradient(circle, rgba(233,73,73,0.3) 0%, rgba(233,74,74,0.1) 50%, rgba(0,0,0,0) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#e94949",endColorstr="#000000",GradientType=1);
		}
}
