.section-how-it-works {
		padding-top: 50px;
		padding-bottom: 50px;
		background-color: #21252a;
		@media (min-width: $break_xl ) {
				padding-top: 100px;
				padding-bottom: 100px;
		}

		.steps {
				@include row-flex();
				margin: 0 auto;
				@media (min-width: $break_md ) {
						display: flex;
						flex-wrap: nowrap;

				}
				@media (min-width: $break_lg ) {
						@include size-xl(10);
						margin: 0 auto;
				}
				&.swiper-wrapper {
						flex-wrap: nowrap;
						@media (min-width: $break_lg ) {
								justify-content: center;
						}
				}




				.step {
						width: 100%;
						max-width: 100%;

						@media (min-width: $break_md ) {
								&:not(.swiper-slide) {
										&:not(:last-child) {
														margin-right: 20px;
										}
								}

						}
						@media (min-width: $break_lg ) {
								width: 315px;
								max-width: 315px;
								&:not(.swiper-slide) {
										&:not(:last-child) {
												margin-right: 20px;
										}
								}
						}
						@media (min-width: $break_xl ) {
								width: 315px;
								max-width: 315px;
								&:not(.swiper-slide) {
										&:not(:last-child) {
												margin-right: 50px;
										}
								}
						}



						&.swiper-slide {
							margin: 0;
						}

						.step__body {
								background-color: #fff;
								padding: 20px 30px 80px;
								text-align: center;
								border-radius: 10px;
								@media (min-width: $break_md ) {
										padding: 20px 30px 50px;
								}
								@media (min-width: $break_lg ) {
										padding: 20px 40px 50px;
								}

								.step__title {
										margin-bottom: 20px;
										h4 {
												font-size: 22px;
												color: #D0359B;
												background: linear-gradient(81.69deg, #26B8DC 0%, #D0359B 100%);
												-webkit-background-clip: text;
												-webkit-text-fill-color: transparent;

										}
								}
						}
						.step__header {
								border-top-right-radius: 10px;
								border-top-left-radius: 10px;
								overflow: hidden;
								img {
										width: 100%;
								}
								& + .step__body {
										border-top-right-radius: 0;
										border-top-left-radius: 0;
								}

						}
				}

		}

.slider-button-prev, .slider-button-next {
		@media (min-width: $break_md ) {
				display: none;
		}
}


		.steps-slider__pagination {
				position: absolute;
				z-index: 2;
				width: 100%;
				justify-content: center;
				display: flex;
				bottom: 40px;
				@media (min-width: $break_md ) {
						position: relative;
						bottom: 0;
						padding-top: 40px;
				}

				.swiper-pagination-bullet {
						width: 10px;
						height: 10px;
						margin: 0 7.5px;
						background: #BDBDBD;
						&.swiper-pagination-bullet-active {
								background: linear-gradient(87.07deg, #DD5645 0%, #EA405E 100%);
						}

				}
				@media (min-width: $break_lg ) {
						display: none;
				}
		}


}

