.section-guaranteed {
		padding-top: 50px;
		padding-bottom: 50px;
		background-color: #21252a;
		@media (min-width: $break_xl ) {
				padding-top: 100px;
				padding-bottom: 100px;
		}

		.guaranteed-wrap {
				color: #fff;

				@media (min-width: $break_md) {
						@include row-flex();
				}


				.guaranteed-block {

						display: flex;
						flex-direction: column;
						align-items: center;
						text-align: center;
						margin-bottom: 30px;

						@media (min-width: $break_md) {
								@include col();
								@include size(4);
								margin-bottom: 0;
						}
						@media (min-width: $break_xl) {
								flex-direction: row;
								text-align: left;
						}
				}

				.guaranteed-block__L {
						margin-bottom: 10px;
						@media (min-width: $break_xl) {
								margin-bottom: 0;
								margin-right: 10px;
						}
				}

				.guaranteed__image {
						min-width: 160px;
						max-width: 160px;
				}
				.guaranteed__title {
						font-size: 22px;
						margin-bottom: 10px;
				}
				.guaranteed__description {
						font-size: 18px;
				}

		}
}

.section-payments {
		padding-top: 50px;
		padding-bottom: 50px;
}
.payments {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;

		@media (min-width: $break_md) {
				flex-direction: row;
		}

		.payments__label {
				margin-bottom: 20px;
				font-weight: 600;


				@media (min-width: $break_md) {
						margin-bottom: 0;
						margin-right: 20px;
				}
		}
		.payments__icons {
				display: flex;
				align-items: center;

				.payments__icon {
						&:not(:last-child) {
								margin-right: 30px;
						}
				}
		}


}